.customize-header-container {
	background-color: white;
	border-radius: var(--card-border-radius);
	box-shadow: var(--box-shadow);
	width: 100%;
	height: 100%;
}

.customize-header-container > .header {
	display: flex;
	justify-content: space-between;
	padding-top: 35px;
}

.customize-header-container > .header > h5 {
	margin: 0;
	word-spacing: 5px;
	padding: 0 0 0 20px;
	border-left: 5px solid #3366ff;
	align-self: center;
	opacity: 0.9;
}

.customize-header-container > .customize-header-wrapper {
	padding-bottom: 20px;
	overflow-y: scroll;
	height: 68vh;
	margin: 0 4px;
	margin-top: 20px;
}

/* cus-header-section-title style start  */
.customize-header-wrapper .cus-header-section-title {
	margin-left: 25px;
	display: flex;
	align-items: center;
}

.customize-header-wrapper .cus-header-section-title > i {
	color: #ff9f1c;
	margin-right: 10px;
	font-size: 20px;
}

.customize-header-wrapper .cus-header-section-title > span {
	font-weight: 500;
	font-size: 16px;
}
/* cus-header-section-title style end  */

/* changing-background-img style start  */
.customize-header-wrapper > .changing-background-img {
	margin-bottom: 20px;
}

.customize-header-wrapper .changing-background-img > .preview-container {
	width: 400px;
	height: 200px;
	border-radius: 5px;
	overflow: hidden;
	margin: 10px 0 0 25px;
	outline: 2px solid rgb(189, 185, 185);
}

.customize-header-wrapper .changing-background-img > .preview-container > img {
	object-fit: fill;
	width: 100%;
	height: 100%;
}

.customize-header-wrapper .changing-background-img > .input-field-container {
	margin: 15px 0 0 25px;
}

.customize-header-wrapper
	.changing-background-img
	> .input-field-container
	> label {
	background-color: #0ead69;
	padding: 8px 20px;
	color: white;
	cursor: pointer;
	border-radius: 5px;
	font-weight: 500;
}

.customize-header-wrapper .changing-background-img > .btn-container {
	margin: 15px 0 0 25px;
}

.customize-header-wrapper .changing-background-img > .btn-container > button {
	padding: 8px 20px;
	color: white;
	cursor: pointer;
	border-radius: 5px;
	font-weight: 500;
}

.customize-header-wrapper
	.changing-background-img
	> .btn-container
	> button:focus {
	box-shadow: none;
}

.customize-header-wrapper
	.changing-background-img
	> .btn-container
	> button:first-child {
	margin-right: 20px;
}
/* changing-background-img style end */

/* cus-header-changing-color style start  */
.customize-header-wrapper > .cus-header-changing-color {
	margin-bottom: 20px;
}

.cus-header-changing-color > .changing-color-container {
	margin-left: 25px;
}

.cus-header-changing-color > .changing-color-container > .section {
	margin-top: 30px;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}

.cus-header-changing-color > .changing-color-container > .section > span {
	font-weight: 500;
}

.cus-header-changing-color
	> .changing-color-container
	> .section
	> .color-picker {
	position: relative;
	width: 150px;
	height: 35px;
	margin-left: 15px;
	border-radius: 5px;
	cursor: pointer;
	z-index: 100;
	border: 2px solid rgb(189, 185, 185);
}

.cus-header-changing-color
	> .changing-color-container
	> .section
	> .color-picker
	> .color-picker-component {
	position: absolute;
	left: 0;
	bottom: 60px;
	z-index: 100;
}

.cus-header-changing-color
	> .changing-color-container
	> .section
	> .color-picker#foreground-color::before {
	content: "foreground - color";
	font-size: 12px;
	position: absolute;
	left: 0px;
	top: -18px;
	color: #264653;
	z-index: 100;
}

.cus-header-changing-color
	> .changing-color-container
	> .section
	> .color-picker#background-color::before {
	content: "background - color";
	font-size: 12px;
	position: absolute;
	left: 0px;
	top: -18px;
	color: #264653;
	z-index: 100;
}

.cus-header-changing-color
	> .changing-color-container
	> .section
	> .color-picker.when-notification-up::before {
	content: "upstairs - color" !important;
}

.cus-header-changing-color
	> .changing-color-container
	> .section
	> .color-picker.when-notification-down::before {
	content: "downstairs - color" !important;
}

.cus-header-changing-color > .changing-color-container > .section > button {
	padding: 0 20px;
	height: 35px;
	font-weight: 500;
	background-color: #0ead69;
	margin-left: 15px;
}

.cus-header-changing-color
	> .changing-color-container
	> .section
	> button:focus {
	box-shadow: none;
}
/* cus-header-changing-color style end  */
